.about {
    background: var(--black);
    color: var(--white);
    width: 100%;

    .content {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        min-height: 25vh;
        padding: 2rem 2rem;
        text-align: center;
        height: 100%;
        width: 100%;

        @include respond(tablet) {
            padding-left: 4rem;
            padding-right: 4rem;
        }

        @include respond(laptop) {
            flex-direction: row;
            padding-left: 8rem;
            padding-right: 8rem;
            min-height: 50vh;
            text-align: left;
        }

        .thumbnail {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 2rem;
            margin-right: 0;
            height: 100px;
            width: 100px;

            @include respond(laptop) {
                height: 150px;
                width: 150px;
                margin-right: 3rem;
                width: 25%;
            }

            img {
                border-radius: 50%;
                max-width: 160px;
                width: 100%;
            }
        }

        .info {
            @include respond(tablet) {
                width: 75%;
            }

            p {
                margin-bottom: 2rem;
            }
        }
    }
}
