.projects-filter {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1rem;
    margin-bottom: 2rem;

    @include respond(laptop) {
        flex-direction: row;
        justify-content: center;
    }

    .filter-selector {
        cursor: pointer;
        padding: 0.4rem 1rem;
        text-align: center;
    }

    .selected-filter {
        border-bottom: 1px solid var(--white);
        opacity: 0.9;
    }

    input[type="radio"] {
        display: none;
    }
}
