*,
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    background: var(--black);
}

.app {
    flex: 1 1 auto;
    font-size: 16px;
    min-height: 100vh;
    overflow-x: hidden;
}

h1,
h2 {
    font-family: $heading-font;
    text-transform: uppercase;
}

h3,
h4,
h5,
h6 {
    font-family: $subheading-font;
}
