@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{--black: #080808;--orange: #f55e2c;--white: #fff}*,*:before,*:after{margin:0;padding:0;box-sizing:border-box}html{background:#080808;background:var(--black)}.app{flex:1 1 auto;font-size:16px;min-height:100vh;overflow-x:hidden}h1,h2{font-family:"Montserrat",sans-serif;text-transform:uppercase}h3,h4,h5,h6{font-family:"Montserrat",sans-serif}.navigation{background:linear-gradient(180deg, #000 0%, rgba(0,0,0,0.6) 80%, rgba(0,0,0,0) 100%);display:block;height:60px;position:fixed;width:100%;z-index:1}@media only screen and (min-width: 600px){.navigation{background:linear-gradient(180deg, #000 0%, rgba(0,0,0,0.6) 60%, rgba(0,0,0,0) 100%)}}.navigation-sub{display:flex;align-items:center;justify-content:center;position:absolute;left:0;right:0;top:24px;width:100%;z-index:1}@media only screen and (min-width: 600px){.navigation-sub{display:block;position:fixed;left:auto;right:36px;width:auto}}.navigation-sub .item{color:#fff;color:var(--white);cursor:pointer;margin-right:30px;padding:0 5px;text-decoration:none;transition:all 0.125s ease-in-out}.navigation-sub .item:last-child{margin-right:0}.navigation-sub .item:hover,.navigation-sub .item:focus{color:#f55e2c;color:var(--orange)}.navigation-sub .item.active{font-weight:bold}.header{color:#fff;color:var(--white);background:url(/static/media/header.8ee042a9.jpeg) center center/cover;background-attachment:fixed;font-size:1.5rem;padding:2rem;position:relative;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translatez(0);-webkit-transform:translatez(0);display:flex;flex-direction:column;align-items:center;justify-content:center;height:300px;width:100%}@media only screen and (min-width: 600px){.header{display:flex;flex-direction:column;align-items:center;justify-content:center;height:500px;width:100%}}.header:before{content:"";background:#080808;background:var(--black);opacity:0.7;position:absolute;z-index:-1;left:0;top:0;height:100%;width:100%}.header .heading,.header .subheading{text-align:center;text-transform:uppercase;z-index:1}.header .heading{font-size:2rem;line-height:1.1}@media only screen and (min-width: 600px){.header .heading{font-size:3rem}}@media only screen and (min-width: 960px){.header .heading{font-size:4rem}}.header .subheading{font-size:0.8rem;line-height:1.5;max-width:200px}@media only screen and (min-width: 600px){.header .subheading{font-size:0.9rem;max-width:none}}@media only screen and (min-width: 960px){.header .subheading{font-size:1rem}}.header .subheading span{border-right:none;display:block}@media only screen and (min-width: 600px){.header .subheading span{display:inline;border-right:1px solid #f55e2c;border-right:1px solid var(--orange);margin-left:10px;margin-right:10px}}.about{background:#080808;background:var(--black);color:#fff;color:var(--white);width:100%}.about .content{display:flex;align-items:center;justify-content:center;flex-direction:column;min-height:25vh;padding:2rem 2rem;text-align:center;height:100%;width:100%}@media only screen and (min-width: 600px){.about .content{padding-left:4rem;padding-right:4rem}}@media only screen and (min-width: 960px){.about .content{flex-direction:row;padding-left:8rem;padding-right:8rem;min-height:50vh;text-align:left}}.about .content .thumbnail{display:flex;align-items:center;justify-content:center;margin-bottom:2rem;margin-right:0;height:100px;width:100px}@media only screen and (min-width: 960px){.about .content .thumbnail{height:150px;width:150px;margin-right:3rem;width:25%}}.about .content .thumbnail img{border-radius:50%;max-width:160px;width:100%}@media only screen and (min-width: 600px){.about .content .info{width:75%}}.about .content .info p{margin-bottom:2rem}.skills{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;height:100%;width:100%;max-width:720px}@media only screen and (min-width: 600px){.skills{justify-content:space-between}}.skills svg{fill:#fff;fill:var(--white);font-size:2rem;margin-bottom:20px;margin-left:10px;margin-right:10px}@media only screen and (min-width: 600px){.skills svg{margin-left:2px;margin-right:2px}}.projects{color:#fff;color:var(--white);background-attachment:fixed;font-size:1.5rem;padding:2rem 2rem;position:relative;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translatez(0);-webkit-transform:translatez(0)}@media only screen and (min-width: 600px){.projects{padding:4rem 2rem}}@media only screen and (min-width: 960px){.projects{padding:4rem 8rem}}.projects:before{content:"";background:#080808;background:var(--black);opacity:0.8;position:absolute;z-index:-1;left:0;top:0;height:100%;width:100%}.projects a{color:#fff;color:var(--white)}.projects h2{margin-bottom:1rem;text-align:center;font-size:1.5rem;line-height:1.1}@media only screen and (min-width: 600px){.projects h2{font-size:2rem}}.projects .content{display:flex;flex-wrap:wrap;justify-content:space-between;height:100%;width:100%;z-index:1}.projects .content:after{content:""}.projects .content:after,.projects .content .project{width:100%}@media only screen and (min-width: 600px){.projects .content:after,.projects .content .project{width:calc(50% - 10px)}}@media only screen and (min-width: 960px){.projects .content:after,.projects .content .project{width:calc(33% - 10px)}}.projects-filter{display:flex;flex-direction:column;align-items:center;font-size:1rem;margin-bottom:2rem}@media only screen and (min-width: 960px){.projects-filter{flex-direction:row;justify-content:center}}.projects-filter .filter-selector{cursor:pointer;padding:0.4rem 1rem;text-align:center}.projects-filter .selected-filter{border-bottom:1px solid #fff;border-bottom:1px solid var(--white);opacity:0.9}.projects-filter input[type="radio"]{display:none}.project{background-color:#080808;background-color:var(--black);display:flex;flex-direction:column;justify-content:space-between;margin-bottom:20px;overflow:hidden}.project.hidden{display:none}.project:hover .image,.project:focus .image{-webkit-transform:scale(1.02);transform:scale(1.02)}.project a{color:#fff;color:var(--white);text-decoration:none}.project .image{transition:all 0.25s ease;-webkit-transform:scale(1);transform:scale(1);width:100%}.project .inner{color:#fff;color:var(--white);display:flex;align-items:center;padding:10px 30px 20px}.project .inner svg{font-size:2rem;margin-right:15px}@media only screen and (min-width: 960px){.project .inner svg{font-size:2rem}}.project .category{font-size:0.8rem}.project .title{font-size:1rem}.footer{background:#080808;background:var(--black);color:#fff;color:var(--white);display:flex;flex-direction:column;align-items:center;justify-content:center;height:25vh;width:100%}@media only screen and (min-width: 600px){.footer{display:flex;flex-direction:column;align-items:center;justify-content:center;height:50vh;width:100%}}.footer h2,.footer p{margin-bottom:2rem}.footer .social-links{display:flex;list-style:none}.footer .social-links li{font-size:1.4rem;margin:0 0.6rem}@media only screen and (min-width: 480px){.footer .social-links li{font-size:2rem;margin:0 1rem}}.footer .social-links a{color:#fff;color:var(--white);transition:all 0.125s ease-in-out}.footer .social-links a:hover,.footer .social-links a:focus{color:#f55e2c;color:var(--orange)}

