// Custom properties
:root {
    --black: #080808;
    --orange: #f55e2c;
    --white: #fff;
}

// Gradients
$orange-gradient: linear-gradient(
    90deg,
    rgba(245, 94, 44, 1) 0%,
    rgba(209, 81, 39, 1) 57%,
    rgba(177, 69, 34, 1) 100%
);

// Fonts
$heading-font: "Montserrat", sans-serif;
$subheading-font: "Montserrat", sans-serif;
