.header {
    color: var(--white);
    background: url("./../Images/header.jpeg") center center / cover;
    background-attachment: fixed;
    font-size: 1.5rem;
    padding: 2rem;
    position: relative;
    backface-visibility: hidden;
    transform: translatez(0);
    -webkit-transform: translatez(0);

    @include flex(300px);

    @include respond(tablet) {
        @include flex(500px);
    }

    &:before {
        content: "";
        background: var(--black);
        opacity: 0.7;
        position: absolute;
        z-index: -1;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
    }

    .heading,
    .subheading {
        text-align: center;
        text-transform: uppercase;
        z-index: 1;
    }

    .heading {
        font-size: 2rem;
        line-height: 1.1;

        @include respond(tablet) {
            font-size: 3rem;
        }

        @include respond(laptop) {
            font-size: 4rem;
        }
    }

    .subheading {
        font-size: 0.8rem;
        line-height: 1.5;
        max-width: 200px;

        @include respond(tablet) {
            font-size: 0.9rem;
            max-width: none;
        }

        @include respond(laptop) {
            font-size: 1rem;
        }

        span {
            border-right: none;
            display: block;

            @include respond(tablet) {
                display: inline;
                border-right: 1px solid var(--orange);
                margin-left: 10px;
                margin-right: 10px;
            }
        }
    }
}
