.skills {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    height: 100%;
    width: 100%;
    max-width: 720px;

    @include respond(tablet) {
        justify-content: space-between;
    }

    svg {
        fill: var(--white);
        font-size: 2rem;
        margin-bottom: 20px;
        margin-left: 10px;
        margin-right: 10px;

        @include respond(tablet) {
            margin-left: 2px;
            margin-right: 2px;
        }
    }
}
