.footer {
    background: var(--black);
    color: var(--white);

    @include flex(25vh);

    @include respond(tablet) {
        @include flex(50vh);
    }

    h2,
    p {
        margin-bottom: 2rem;
    }

    .social-links {
        display: flex;
        list-style: none;

        li {
            font-size: 1.4rem;
            margin: 0 0.6rem;

            @include respond(mobile) {
                font-size: 2rem;
                margin: 0 1rem;
            }
        }

        a {
            color: var(--white);
            transition: all 0.125s ease-in-out;

            &:hover,
            &:focus {
                color: var(--orange);
            }
        }
    }
}
