.project {
    background-color: var(--black);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 20px;
    overflow: hidden;
    // transition: all 0.25s linear;

    &.hidden {
        display: none;
    }

    // &.visually-hidden {
    //     opacity: 0;
    // }

    &:hover,
    &:focus {
        .image {
            transform: scale(1.02);
        }
    }

    a {
        color: var(--white);
        text-decoration: none;
    }

    .image {
        transition: all 0.25s ease;
        transform: scale(1);
        width: 100%;
    }

    .inner {
        color: var(--white);
        display: flex;
        align-items: center;
        padding: 10px 30px 20px;

        svg {
            font-size: 2rem;
            margin-right: 15px;

            @include respond(laptop) {
                font-size: 2rem;
            }
        }
    }

    .category {
        font-size: 0.8rem;
    }

    .title {
        font-size: 1rem;
    }
}
