.navigation {
    background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 1) 0%,
        rgba(0, 0, 0, 0.6) 80%,
        rgba(0, 0, 0, 0) 100%
    );
    display: block;
    height: 60px;
    position: fixed;
    width: 100%;
    z-index: 1;

    @include respond(tablet) {
        background: linear-gradient(
            180deg,
            rgba(0, 0, 0, 1) 0%,
            rgba(0, 0, 0, 0.6) 60%,
            rgba(0, 0, 0, 0) 100%
        );
    }
}

.navigation-sub {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 24px;
    width: 100%;
    z-index: 1;

    @include respond(tablet) {
        display: block;
        position: fixed;
        left: auto;
        right: 36px;
        width: auto;
    }

    .item {
        color: var(--white);
        cursor: pointer;
        margin-right: 30px;
        padding: 0 5px;
        text-decoration: none;
        transition: all 0.125s ease-in-out;

        &:last-child {
            margin-right: 0;
        }

        &:hover,
        &:focus {
            color: var(--orange);
        }
    }

    .item.active {
        font-weight: bold;
    }
}
