.projects {
    color: var(--white);
    // background: url("./../Images/projects.jpeg") center center / cover;
    background-attachment: fixed;
    font-size: 1.5rem;
    padding: 2rem 2rem;
    position: relative;
    backface-visibility: hidden;
    transform: translatez(0);
    -webkit-transform: translatez(0);

    @include respond(tablet) {
        padding: 4rem 2rem;
    }

    @include respond(laptop) {
        padding: 4rem 8rem;
    }

    &:before {
        content: "";
        background: var(--black);
        opacity: 0.8;
        position: absolute;
        z-index: -1;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
    }

    a {
        color: var(--white);
    }

    h2 {
        margin-bottom: 1rem;
        text-align: center;

        font-size: 1.5rem;
        line-height: 1.1;

        @include respond(tablet) {
            font-size: 2rem;
        }
    }

    .content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        height: 100%;
        width: 100%;
        z-index: 1;

        &:after {
            content: "";
        }

        &:after,
        .project {
            width: 100%;

            @include respond(tablet) {
                width: calc(50% - 10px);
            }

            @include respond(laptop) {
                width: calc(33% - 10px);
            }
        }
    }
}
